<style scoped>
    .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }
    .layout-header-bar{
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
</style>
<template>
    <div class="layout">
        <Sider :style="{position: 'fixed', height: '100vh', left: 0, overflow: 'auto'}">
            <Menu active-name="1-2" theme="dark" width="auto" :open-names="['1']">
                <Submenu name="1">
                    <template slot="title">
                        <Icon type="ios-navigate"></Icon>
                        Item 1
                    </template>
                    <MenuItem name="1-1">Option 1</MenuItem>
                    <MenuItem name="1-2">Option 2</MenuItem>
                    <MenuItem name="1-3">Option 3</MenuItem>
                </Submenu>
                <Submenu name="2">
                    <template slot="title">
                        <Icon type="ios-keypad"></Icon>
                        Item 2
                    </template>
                    <MenuItem name="2-1">Option 1</MenuItem>
                    <MenuItem name="2-2">Option 2</MenuItem>
                </Submenu>
                <Submenu name="3">
                    <template slot="title">
                        <Icon type="ios-analytics"></Icon>
                        Item 3
                    </template>
                    <MenuItem name="3-1">Option 1</MenuItem>
                    <MenuItem name="3-2">Option 2</MenuItem>
                </Submenu>
            </Menu>
        </Sider>
        <Layout :style="{marginLeft: '200px'}">
            <Header :style="{background: '#fff', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}"></Header>
            <Content :style="{padding: '0 16px 16px'}">
                <Breadcrumb :style="{margin: '16px 0'}">
                    <BreadcrumbItem>Home</BreadcrumbItem>
                    <BreadcrumbItem>Components</BreadcrumbItem>
                    <BreadcrumbItem>Layout</BreadcrumbItem>
                </Breadcrumb>
                <Row>
              
                  <DatePicker
                    :open="open"
                    :value="value3"
                    confirm
                    type="date"
                    format="yyyy/M/dd"
                    
                    @on-change="handleChange"
                    @on-clear="handleClear"
                    @on-ok="handleOk">
                    <a href="javascript:void(0)" @click="handleClick">
                        <Icon type="ios-calendar-outline"></Icon>
                        <template v-if="value3 === ''">Select date</template>
                        <template v-else>{{ value3 }}</template>
                    </a>
                </DatePicker>
                              
                </Row>

                <Button type="primary" @click="submitpost">查询</Button>
                <Card>
                    <div class="demo-split">
                    <Split v-model="split3">
                        <div slot="right" class="demo-split-pane no-padding">
                            <!--
                            <Split v-model="split4" mode="vertical">
                            -->
                                <div slot="top" class="demo-split-pane" id="text">
                                    <!--
                                    <List border>
                                    <ListItem>Title:{{showdata.title}}</ListItem>
                                    <ListItem>Date:{{showdata.date}}</ListItem>
                                    <ListItem>Authors：{{showdata.author}}</ListItem>
                                    <ListItem><i>Content:</i>
                                    <p><text-highlight :queries="showdata.entities">{{showdata.content }}</text-highlight></p>
                                    </ListItem>
                                    </List> 
                                    -->
                                    <div style="height:300px;width:1300px;background-color:#f5f7f9;">

                                    <happy-scroll color="rgba(0,0,0,0.5)" size="5">
                                        <!-- 内层盒子——内容区 -->
                                        <div class="con">
                                        <List border>
                                            <ListItem>Title:{{showdata.title}}</ListItem>
                                            <ListItem>Date:{{showdata.date}}</ListItem>
                                            <ListItem>Authors:</ListItem>
                                            <ListItem>{{showdata.author}}</ListItem>
                                            <ListItem>Content:</ListItem>
                                            <ListItem><text-highlight :queries="queries">{{showdata.content}}</text-highlight>
                                            </ListItem>
                                            
                                            </List>
                                        </div>
                                    </happy-scroll>
                                    </div>

                                    <!--
                                    <Split v-model="split5">
                                    
                                    <div slot="right" class="demo-split-pane" id="123">
                                        aaa
                                    </div>
                                    
                                    </Split>
                                    -->
                                </div>
                                <div slot="bottom" class="demo-split-pane">
                                    
                                    <Table border :no-data-text="loadingText ? loadingText : '暂无数据'" :height="260" :columns="columns7" :data="similarinfo"></Table>

                                </div>
                            <!--
                            </Split>
                            -->
                        </div>
                        
                        <div slot="left" class="demo-split-pane" id="123">                 
                            <Table border height="570" :columns="columns1" :data="CurrentDateList"></Table> 
                        </div>
                    </Split>
                </div>
                </Card>
                
            </Content>
        </Layout>
    </div>
</template>

<script>
    export default {
        //render渲染
        // name='123',
        // render:
        
        data () {
            return {
                loadingText: '',
                open:false,
                value3:'',
                date:{date:''},
                queries:[],//需要高亮的文字
                arry:[],

                split3: 0.2,
                split4: 0.9,
                split5:0.4,
                
                columns1:[
                    {
                        title:'Title',
                        key: 'title',                      
                        //选择框
                        render: (h, params) => {
                            return h('div', [
                                h('Tag', {
                                    props: {
                                        type: 'info',
                                        size: 'small',
                                        color:"primary",
                                        
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    
                                    
                                }, params.row.type),

                                h('Button', {
                                    props: {
                                        type: 'text',
                                        size: 'small',
                                        
                                    },
                                    style: {
                                        marginRight: '6px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, params.row.title)

                                
                        ]);
                        }
                    }
                ],
                columns7: [
                    {
                        title: 'Title',
                        key: 'title',
                        render: (h, params) => {
                            return h('div', [
                                h('Icon', {
                                    props: {
                                        type: 'title'
                                    }
                                }),
                                h('strong', params.row.title)
                            ]);
                        }
                    },
                    {
                        title: 'Author',
                        key: 'author'
                    },
                    {
                        title: 'Date',
                        key: 'date'
                    },
                    {
                        title: 'Entities',
                        key: 'entities'
                    },

                    {
                        title: 'Action',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.showinfo(params.index)
                                        }
                                    }
                                }, 'View'),
                                h('Button', {
                                    props: {
                                        type: 'success',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.load(params.index)
                                        }
                                    }
                                }, 'Load'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.del(params.index)
                                        }
                                    }
                                }, 'Del')
                            ]);
                        }
                    }
                ],
                data6: [
                    {
                        name: 'John Brown',
                        age: 18,
                        address: 'New York No. 1 Lake Park'
                    },
                    {
                        name: 'Jim Green',
                        age: 24,
                        address: 'London No. 1 Lake Park'
                    },
                    {
                        name: 'Joe Black',
                        age: 30,
                        address: 'Sydney No. 1 Lake Park'
                    },
                    {
                        name: 'Jon Snow',
                        age: 26,
                        address: 'Ottawa No. 2 Lake Park'
                    }
                ],
                
                //当日所有知识
                resourceList:[
                    {
                        date:'2020/2/11',
                        title:'人民日报1',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa d bbb e ddd  fs',
                        entities:['d','e','f'],

                    },

                    {
                        date:'2020/2/11',
                        title:'人民日报2',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },

                    {
                        date:'2020/02/11',
                        title:'人民日报3',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },
                    {
                        date:'2020/02/11',
                        title:'人民日报4',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },

                    {
                        date:'2020/02/11',
                        title:'广州日报1',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },

                    {
                        date:'2020/02/11',
                        title:'广州日报2',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },

                    {
                        date:'2020/02/11',
                        title:'广州日报3',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },
                    {
                        date:'2020/02/11',
                        title:'广州日报4',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },

                    {
                        date:'2020/02/11',
                        title:'羊城晚报1',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },

                    {
                        date:'2020/02/11',
                        title:'羊城晚报2',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },

                    {
                        date:'2020/02/11',
                        title:'羊城晚报3',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },
                    {
                        date:'2020/02/11',
                        title:'羊城晚报4',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },
                    
                    {
                        date:'2020/02/12',
                        title:'羊城晚报3',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },
                    {
                        date:'2020/02/12',
                        title:'羊城晚报4',
                        author:['a','b','c'],
                        type:'news',
                        content:'aaaaaaaaaa',
                        entities:['d','e','f'],

                    },

                    

                ],
                
                //存放新闻实体数据的list
                CurrentDateList:[ 
                        
                ],
                
                //存放相关新闻题目的list：
                similartitle:[],

                //存放相关新闻所有信息的list：
                similarinfo:[],

                
                showdata:{},
                
            }
        },
        
        

         methods:{


             //展示数据，包括基本展示以及相似十条数据的展示
            async show (index) {
                // this.$Modal.info({
                //     title: 'News Info',
                //     content: `Content：${this.resourceList[index].content}<br>Entities：${this.resourceList[index].entities}`
                // })
                this.loadingText = '数据加载中...'
                this.showdata=this.CurrentDateList[index]
                this.queries=this.CurrentDateList[index].entities.split(",")
                this.similartitle.splice(0,this.similartitle.length)
                this.similarinfo.splice(0,this.similarinfo.length)
                await this.$axios.post('/find_similar_title',this.$qs.stringify({title:this.CurrentDateList[index].title}))
                .then((response) =>{  
                                
                    this.similartitle=response.data;
                                                                 
                }).catch(function (error) {
               console.log(error)
               })
               
               if(this.similartitle.length != 0){
                   for(var i=0;i < this.similartitle.length;i++){
                       await this.$axios.post('/title_find_data',this.$qs.stringify({title:this.similartitle[i]}))
                        .then((response) =>{  
                                      
                            this.similarinfo.push(response.data[0])
                                                                                                 
                        }).catch(function (error) {
                        console.log(error)
                    })
                   }
               } 
               
                            //this.CurrentDateList.splice(0,this.CurrentDateList.length)
            },

            load(index){
                this.showdata=this.similarinfo[index]
                this.queries=this.similarinfo[index].entities.split(",")
            },


            showinfo (index) {
                
                this.arry=this.similarinfo[index].entities.split(",")
                console.log(this.arry)
                this.$Modal.info({
                    model:"modal1",
                    title: 'Data Info',
                    width:'900',
                    content: `Title：${this.similarinfo[index].title}<br>Date：${this.similarinfo[index].date}<br>Author：${this.similarinfo[index].author}
                    <br>Entities：${this.similarinfo[index].entities}<br>Content：<br>${this.similarinfo[index].content}`
                })
                
            },           




            handleClick () {
                this.open = !this.open;
            },
            handleChange (date) {
                this.value3 = date;
            },
            handleClear () {
                this.open = false;
            },
            handleOk () {
                this.open = false;
            },

            submit(){
                this.CurrentDateList.splice(0,this.CurrentDateList.length)
                for(var i=0;i < this.resourceList.length;i++){
                    if(this.resourceList[i].date === this.value3){
                        this.CurrentDateList.push(this.resourceList[i])
                    }
                }
            },
            // submit(){
            //     for(var i=0;i < this.resourceList.length;i++){
                   
            //             this.CurrentDateList.push(this.resourceList[i])
                  
            //     }
            // },

            //api
            async submitpost() { 
                //首先清空数组
                this.CurrentDateList.splice(0,this.CurrentDateList.length)
                await this.$axios.post('/knowledge',this.$qs.stringify({date:this.value3}))
                .then((response) =>{  
                                
                    this.CurrentDateList=response.data;
                    console.log(this.CurrentDateList)                                             
                }).catch(function (error) {
               console.log(error)
            })
            },
                

        },

        
        
        // computed: {
            
        //     searchList: function() {
        //     var _this = this;
        //     //逻辑-->根据input的value值筛选goodsList中的数据
        //     var arrBySearch = []; //声明一个空数组来存放数据
        //     for (var i = 0; i < this.CurrentDateList.length; i++) {
        //         //判断输入框中的值是否可以匹配到数据，如果匹配成功
        //         arrBySearch.push(this.CurrentDateList[i]);
        //     }
        //     //一定要记得返回筛选后的数据
        //     return arrBySearch;
        //     }
            
        // },
        
        

    }

    
</script>
<style>
    .demo-split{
        height: 600px;
        border: 1px solid #dcdee2;
    }
    .demo-split-pane{
        padding: 10px;
    }
    .demo-split-pane.no-padding{
        height: 300px;
        padding: 0;
    }
    .demo-split-pane2{
        padding: 10px;
    }
</style>



