import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import View from '@/components/View'
import Table from '@/components/Table'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Table',
      component: Table
    },
    {
      path: '/view',
      name: 'View',
      component: View
    },
    {
      path: '/table',
      name: 'Table',
      component: Table
    }
  ]
})



